.loaderBox {
    position: fixed !important;
    top: 50% !important;
    left: 45% !important;
    z-index: 1;
    height: 80;
    width: 80;
}

.cursor-pointer {
    cursor: pointer;
}

@media (max-width: 992px) {    
}
@media (max-width: 767px) {
    .loaderBox {
        left: 40% !important;
    }
}
@media (max-width: 576px) {
    .loaderBox {
        left: 35% !important;
    }
}
